import Head from 'next/head'
import Layout, { siteTitle } from '../components/layout/layout'
import utilStyles from '../styles/utils.module.css'
import tabStyles from '../styles/tab.module.css'
import Link from 'next/link'
import { getSortedPostsData } from '../lib/posts'
import Date from '../components/Date'
import { useEffect, useState } from 'react'
import { otherCategoryPostsDirectory, jsCategoryPostsDirectory, techCategoryPostsDirectory } from '../lib/constants'

export default function Home({otherPosts, jsPosts, techPosts, theme}) {
  const [tab, setTab] = useState([
    {
      open: true, name: "#js", posts: jsPosts
    },
    // {
    //   open: false, name: "#tech", posts: techPosts
    // },
    {
      open: false, name: "#others", posts: otherPosts
    } 
  ]);

  const selectTab = (tabProp) => {
    let newTab = [...tab];
    for (let i = 0; i < newTab.length; i++) {
      if (tabProp.name == newTab[i].name) {
        newTab[i].open = true;
      } else {
        newTab[i].open = false;
      }
    }
    setTab(newTab);
  };
  
  const handleClick = (tabProp) => {
    if (!tabProp.open) {
      selectTab(tabProp);
      localStorage.setItem('category', tabProp.name);
    }
  };

  useEffect(() => {
    const category = localStorage.getItem('category')
    if (category) {
      selectTab({ open: false, name: category });
    } else {
      localStorage.setItem('category', tab[0].name);
    }
  }, [])

  return (
    <Layout home theme={theme}>
      <Head>
        <title>{siteTitle}</title>
      </Head>
      <section className={`${utilStyles.headingMd} ${utilStyles.padding1px}`}>
        <div className={tabStyles.header}>
          {
            tab.map((tabProp, index) => (
              <>
                <h2 key={index} className={`${utilStyles.headingLg} ${theme && utilStyles.whiteText} ${index != 0 && utilStyles.ml8}`}>
                  <a onClick={() => handleClick(tabProp)} 
                    className={`${theme ? 'dark' : null} ${tabStyles.tab} ${tabProp.open && tabStyles.selected}`}>
                    {tabProp.name}
                  </a>
                </h2>
                {
                  tabProp.open && 
                  <ul className={utilStyles.list}>
                    {tabProp.posts.map(({ id, date, title }, index) => (
                      <li key={index} className={utilStyles.listItem} key={id}>
                        <Link href="/posts/js/[id]" as={`/posts/js/${id}`}>
                          <a className={theme ? 'dark' : null}>{title}</a>
                        </Link>
                        <br />
                        <small className={utilStyles.lightText}>
                          <Date dateString={date} />
                        </small>
                      </li>
                    ))}
                  </ul>
                }
              </>
            ))
          }
        </div>

        
        {/* {
          tab[0].open &&
          <ul className={utilStyles.list}>
            {jsPosts.map(({ id, date, title }, index) => (
              <li key={index} className={utilStyles.listItem} key={id}>
                <Link href="/posts/js/[id]" as={`/posts/js/${id}`}>
                  <a className={theme ? 'dark' : null}>{title}</a>
                </Link>
                <br />
                <small className={utilStyles.lightText}>
                  <Date dateString={date} />
                </small>
              </li>
            ))}
          </ul>
        }

        {
          tab[1].open &&
          <ul className={utilStyles.list}>
            {techPosts.map(({ id, date, title }, index) => (
              <li key={index} className={utilStyles.listItem} key={id}>
                <Link href="/posts/tech/[id]" as={`/posts/tech/${id}`}>
                  <a className={theme ? 'dark' : null}>{title}</a>
                </Link>
                <br />
                <small className={utilStyles.lightText}>
                  <Date dateString={date} />
                </small>
              </li>
            ))}
          </ul>
        }

        {
          tab[2].open &&
          <ul className={utilStyles.list}>
            {otherPosts.map(({ id, date, title }, index) => (
              <li key={index} className={utilStyles.listItem} key={id}>
                <Link href="/posts/others/[id]" as={`/posts/others/${id}`}>
                  <a className={theme ? 'dark' : null}>{title}</a>
                </Link>
                <br />
                <small className={utilStyles.lightText}>
                  <Date dateString={date} />
                </small>
              </li>
            ))}
          </ul>
        } */}

        
      </section>
    </Layout>
  )
}

export async function getStaticProps() {
  const otherPosts = getSortedPostsData(otherCategoryPostsDirectory)
  const jsPosts = getSortedPostsData(jsCategoryPostsDirectory)
  const techPosts = getSortedPostsData(techCategoryPostsDirectory)
  return {
    props: {
      otherPosts, jsPosts, techPosts
    }
  }
}